import React, { useState } from 'react'
import wind from './wind.svg'
import globe from './globe.svg'
import {
    Header, Location, Input, MainWeather, MainContainer, Property, Value, Button,
    Details, WindContainer, CityInfo, Icon, Temperature, H2, H3, OtherDetails, Footer
} from './styles'
import axios from 'axios'
import { StyleSheetManager } from 'styled-components'


const WeatherApp = () => {
    const [city, setCity] = useState('halol')
    const [icon, setIcon] = useState('01d')
    const [temperature, setTemperature] = useState('30')
    const [weather, setWeather] = useState('clouds')
    const [desc, setDesc] = useState('few clouds')
    const [feelsLike, setFeelsLike] = useState('30.5')
    const [maxTemp, setMaxTemp] = useState('30.75')
    const [minTemp, setMinTemp] = useState('30.75')
    const [presure, setPressure] = useState('1007')
    const [humidity, setHumidity] = useState('41')
    const [seaLevel, setSelLevel] = useState('1007')
    const [grndLevel, setGrndLevel] = useState('995')
    const [visiblity, setVisiblity] = useState('10000')
    const [speed, setSpeed] = useState('3.41')
    const [deg, setDeg] = useState('245')
    const [gust, setGust] = useState('4.18')
    const [country, setCountry] = useState('IN')
    const [timezone, setTimeZone] = useState('19800')

    const iconUrl = `http://openweathermap.org/img/wn/${icon}@2x.png`
    const proxy = 'http://cors-anywhere.herokuapp.com/'

    const getWeather = () => {
        axios.get(`http://api.openweathermap.org/data/2.5/weather?q=${city}&appid=248249feaaf37fd426460b1713b85c47&units=metric`)
            .then(res => {
                console.log(res.data)
                setTemperature(res.data.main.temp)
                setWeather(res.data.weather[0].main)
                setDesc(res.data.weather[0].description)
                setFeelsLike(res.data.main.feels_like)
                setMaxTemp(res.data.main.temp_max)
                setMinTemp(res.data.main.temp_min)
                setPressure(res.data.main.pressure)
                setHumidity(res.data.main.humidity)
                setSelLevel(res.data.main.sea_level)
                setGrndLevel(res.data.main.grnd_level)
                setVisiblity(res.data.visibility)
                setSpeed(res.data.wind.speed)
                setDeg(res.data.wind.deg)
                setGust(res.data.wind.gust)
                setCountry(res.data.sys.country)
                setCity(res.data.name)
                setTimeZone(res.data.timezone)
                setIcon(res.data.weather[0].icon)
            })
            .catch(err => console.log(err))
    }

    return (
        <div>
            <Header>My Weather App</Header>
            <Location>
                <Input type="text" name="city" placeholder="select city" value={city} onChange={e => setCity(e.target.value)} />
                <Button onClick={getWeather}>Go!</Button>
            </Location>
            <MainContainer>
                <MainWeather>
                    <Icon><img src={iconUrl} width='120px' alt="icon" /></Icon>
                    <Temperature>{temperature} °C</Temperature>
                    <H2>{weather}</H2>
                    <H3>{desc}</H3>
                    <OtherDetails>
                        <Property>Feels Like :<Value>{feelsLike} °C</Value></Property>
                        <Property>MAX Temperature :<Value>{maxTemp} °C</Value></Property>
                        <Property>MIN Temperature :<Value>{minTemp} °C</Value></Property>
                        <Property>Pressure :<Value>{presure} hPa</Value></Property>
                        <Property>Humidity :<Value>{humidity} %</Value></Property>
                        <Property>Sea Level :<Value>{seaLevel}</Value></Property>
                        <Property>Ground Level :<Value>{grndLevel}</Value></Property>
                        <Property>Visibility :<Value>{visiblity} m</Value></Property>
                    </OtherDetails>
                </MainWeather>
                <Details>
                    <WindContainer>
                        <Icon><img src={wind} width='50px' alt="icon" /></Icon>
                        <OtherDetails>
                            <Property>Speed :<Value>{speed} m/s</Value></Property>
                            <Property>Deg :<Value>{deg}°</Value></Property>
                            <Property>Gust :<Value>{gust} knots</Value></Property>
                        </OtherDetails>
                    </WindContainer>
                    <CityInfo>
                        <Icon><img src={globe} width='50px' alt="icon" /></Icon>
                        <OtherDetails>
                            <Property>Country :<Value>{country}</Value></Property>
                            <Property>City :<Value>{city}</Value></Property>
                            <Property>Timezone :<Value>{timezone}</Value></Property>
                        </OtherDetails>
                    </CityInfo>
                </Details>
            </MainContainer>
            <Footer>
                &copy;krutik Gadhiya &nbsp; <a href='https://openweathermap.org/' target='_blank'><p>Powered By Open weathermap</p></a>
            </Footer>
        </div>
    )
}

export default WeatherApp